<!---->
<template>
  <div class="impower-agent">
    <div class="impower-agent-title"> - </div>
    <Form :model="formItem" ref="form" :rules="ruleForm" :label-width="150">
      <FormItem label=":" prop="nickname">
        <Input v-model="formItem.nickname" :maxlength="6" type='text' placeholder="..."></Input>
      </FormItem>
      <FormItem label=":" prop="account">
        <Input v-model="formItem.account" :maxlength="11" type='text' placeholder="\..."></Input>
      </FormItem>
      <!--<FormItem label=":" prop="card">
        <Input v-model="formItem.card" :minlength="15" :maxlength="18" type='text' placeholder="..."></Input>
      </FormItem>-->
      <FormItem label=":">
        <RadioGroup v-model="type">
          <Radio label="1">
            <span></span>
          </Radio>
          <!--<Radio label="2">
            <span></span>
          </Radio>-->
        </RadioGroup>
      </FormItem>
      <!--          <FormItem label=":">
                  <v-distpicker @province="onChangeProvince" @city="onChangeCity" @area="onChangeArea" :province="deafultProvince" :city="deafultCity" :area="deafultArea"></v-distpicker>
                </FormItem>-->
      <FormItem>
        <Button type="primary" @click="save('form')"></Button>
        <Button type="ghost" @click="handleReset('form')" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import md5 from "js-md5";
  import VDistpicker from "v-distpicker";
  export default {
    components: {
      VDistpicker
    },
    data() {
      const vaild = (rule, value, callback) => {
        this.$http
          .get(global.url.commerce_nickname_find, {
            nickname: value
          })
          .then(result => {
            if (result) {
              callback(new Error(""));
            } else callback();
          });
      };
      const exists = (rule, value, callback) => {
        var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (!Number.isInteger(+value) || parseInt(value) <= 0) {
          callback(new Error(""));
        }else if(!myreg.test(value)){
          callback(new Error(""));
        } else if (value) {
          this.$http
            .get(global.url.commerce_find, {
              mobile: value
            })
            .then(result => {
              if (result) {
                callback(new Error(""));
              } else callback();
            });
        } else {
          callback();
        }
      };
      return {
        deafultProvince : "",
        deafultCity : "",
        deafultArea: "",
        tab: "list",
        city: "",
        seachValue: "", //
        pageCount: 0, //
        pageSize: global.page.size,
        type: "1",
        formItem: {
          account: "",
          nickname: "",
          card: ""
        },
        ruleForm: {
          nickname: [
            {
              required: true,
              message: "",
              trigger: "blur"
            },
            {
              validator: vaild,
              trigger: "blur"
            }
          ],
          account: [
            {
              required: true,
              message: "",
              trigger: "blur"
            },
            {
              validator: exists,
              trigger: "blur"
            }
          ]
        },
        tableColumns: [
          {
            title: "",
            align: "center",
            key: "mobile"
          },
          {
            title: "",
            align: "center",
            key: "position",
            render(h, params) {
              let _str = '';
              if(params.row.user_type == 1 ){
                _str =  "";
              }else{
                _str =  "";
              }
              return h("div",
                _str
              );
            }
          },
          {
            title: "",
            align: "center",
            key: "nickname"
          },
          {
            title: "",
            align: "center",
            key: "balance"
          },
          {
            title: "",
            align: "center",
            key: "recommend_code"
          },
          {
            title: "",
            align: "center",
            key: "location"
          },
          {
            title: "",
            align: "center",
            key: "identity_card"
          },
          {
            title: "",
            align: "center",
            key: "bank_name"
          },
          {
            title: "",
            align: "center",
            key: "bank_number"
          },
          {
            title: "",
            align: "center",
            key: "create_account_name"
          },
          {
            title: "",
            key: "disabled",
            align: "center",
            render(h, params) {
              let _str = '';
              if(params.row.disabled == '1' ){
                _str = '';
              }else{
                _str = '';
              }
              return h("div", _str);
            }
          },
          {
            title: "",
            key: "createt_time",
            align: "center",
            render(h, params) {
              let tempb = new Date(params.row.createt_time);
              return h("div",
                tempb.getFullYear() +
                "-" +
                (parseInt(tempb.getMonth()) + 1) +
                "-" +
                tempb.getDate()
              );
            }
          },
          {
            title: "",
            align: "center",
            render: (h, params) => {
              let reamrk = params.row.disabled ? "" : "";
              return h("div", [
                h("Button", [
                  h(
                    "Poptip",
                    {
                      props: {
                        confirm: true,
                        title: "?"
                      },
                      on: {
                        "on-ok": () => {
                          this.disable(params.row.account_id);
                        }
                      }
                    },
                    reamrk
                  )
                ]),
                h("Button", [
                  h(
                    "Poptip",
                    {
                      props: {
                        confirm: true,
                        title: "?"
                      },
                      on: {
                        "on-ok": () => {
                          this.restartPwd(params.row.account_id);
                        }
                      }
                    },
                    ""
                  )
                ])
              ]);
            }
          }
        ],
        data: []
      };
    },
    created() {

    },
    methods: {
      onChangeProvince(data) {
        this.deafultProvince = data.value;
      },
      onChangeCity(data) {
        this.deafultCity = data.value;
      },
      onChangeArea(data) {
        this.deafultArea = data.value;
        this.city = data.code;
      },
      exists() {
        let self = this;
      },
      restartPwd(id) {
        let self = this;
        self.$http
          .post(global.url.commerce_restart, {
            aid: id
          })
          .then(result => {
            self.$Message.success("!");
          });
      },
      restart(){
        this.deafultProvince = "";
        this.deafultCity = "";
        this.deafultArea = "";
      },
      handleReset(name) {
        this.$refs[name].resetFields();
      },
      save(name) {
        let self = this;

        self.$refs[name].validate(vaild => {
          if (vaild) {
            /*          if (!self.deafultProvince) {
                        self.$Message.error("");
                        return;
                      }
                      if (!self.deafultCity) {
                        self.$Message.error("");
                        return;
                      }
                      if (!self.deafultArea) {
                        self.$Message.error("");
                        return;
                      }

                      var _address = self.deafultProvince + self.deafultCity + self.deafultArea;*/

            self.$http
              .post(global.url.commerce_add, {
                mobile: self.formItem.account,
                pwd: md5(self.formItem.account),
                nickname: self.formItem.nickname,
                location: '',
                city: self.city,
                card: self.formItem.card,
                type: self.type
              })
              .then(result => {
                if (result) {
                  self.tab = "list";
                  self.$refs[name].resetFields();
                  self.$Message.success("");
                  self.restart();
                  this.$router.push('/operation/commerce_list');
                } else {
                  self.$Message.error("");
                }
              });
          }
        });
      },
    }
  };
</script>



